(function( factory ) {
	if ( typeof define === "function" && define.amd ) {
		define("jquery.validate",["jquery",'jquery.widget', "jquery.validate.core"], factory );
	} else {
		factory( jQuery,widget );
	}
}(function( $,widget ) {

  /**
   * 
   */
  $.validator.addMethod('enCode',function(value,element,param){
    return this.optional(element) || !/[^a-zA-Z\s]/.test(value);
  },$.validator.format(""));

  /**
   * 
   */

  $.validator.addMethod('zhCode',function(value,element,param){
    return this.optional(element) || /^[\u2E80-\u9FFF\s]+$/.test(value);
  },$.validator.format(""));

  /**
   * 
   */
  $.validator.addMethod('mobile',function(value,element,param){
    return this.optional(element) || /^\+?(86)?-?1\d{10}$/.test(value);
  },$.validator.format(""));

  /**
   * ()
   */
  $.validator.addMethod('phone',function(value,element,param){
    return this.optional(element) || /^(\d{3,4}-?)?\d{8}$/.test(value);
  },$.validator.format(""));

  /**
   * 
   */
  $.validator.addMethod('mobilePhone',function(value,element,param){
    return this.optional(element) || /^\+?(86)?-?1\d{10}$/.test(value) || /^(\d{3,4}-?)?\d{8}$/.test(value);
  },$.validator.format(""));

  /**
   * 
   */
  
  $.validator.addMethod('multiRequire',function(value,element,param){
    var res = true,that = this;
    var $others = $('[multirequire='+param+']',this.currentForm).not(element);

    if(!$.trim(value)){//
      res = false;
      $others.each(function(){
        //
        if($.trim($(this).val())){
          res = true;
          return false;
        }
      });
    }else{//
      setTimeout(function(){
        $others.each(function(){
          that.element(this);
        });
      },0);
    }

    return res;
  },$.validator.format(""));

  /**
   * CSA
   */
  $.validator.addMethod('casCode',function(value,element,param){
    return this.optional(element) || /^\d+\-\d+\-\d+$/.test(value);
  },$.validator.format("CSA"));


  /*  
   * Translated default messages for the jQuery validation plugin.
   * Locale: ZH (Chinese,  (Zhngwn), , )
   */
  $.extend($.validator.messages, {
    required: "",
    remote: "",
    email: "",
    url: "",
    date: "",
    dateISO: " (YYYY-MM-DD)",
    number: "",
    digits: "",
    creditcard: "",
    equalTo: "",
    extension: "",
    maxlength: $.validator.format(" {0} "),
    minlength: $.validator.format(" {0} "),
    rangelength: $.validator.format(" {0}  {1} "),
    range: $.validator.format(" {0}  {1} "),
    max: $.validator.format(" {0} "),
    min: $.validator.format(" {0} ")
  });

  /**
   * 
   */
  var defaultsHighlight = $.validator.defaults.highlight;
  $.validator.setDefaults({
    ignore : ".ignore",
    errorClass : "validate-error",
    success : function(label){
      //classfa-check
      label.addClass('validate-success');
    },
    errorPlacement : function(error,element){
      element.parent().append(error);
    },
    highlight : function(el,errorClass){
      var $el = $(el);
      $el.siblings("."+errorClass).removeClass('validate-success');
      defaultsHighlight.apply(this,arguments);
    }
  });

  return $;


}));