/**
 * jqueryui timepicker
 */
(function( factory ) {
  if ( typeof define === "function" && define.amd ) {
    define('jquery.timepicker',['jquery','jquery.widget','jquery.ui','jquery.datetimepicker'],factory);
  } else {
    factory( jQuery,widget );
  }
}(function($,widget){

  //jquery-uitimepicker
  var $_fn_timepicker = $.fn.datetimepicker;

  $.timepicker.regional['zh-CN'] = {
    timeOnlyTitle: '',
    timeText: '',
    hourText: '',
    minuteText: '',
    secondText: '',
    millisecText: '',
    microsecText: '',
    timezoneText: '',
    currentText: '',
    closeText: '',
    timeFormat: 'HH:mm',
    timeSuffix: '',
    amNames: ['AM', 'A'],
    pmNames: ['PM', 'P'],
    isRTL: false
  };
  $.timepicker.setDefaults($.timepicker.regional['zh-CN']);

  var Timepicker = function(opt){
    this.options = $.extend(true,{},arguments.callee.options,opt);
    this.$el = $(this.options.el);
    //jquery-uitimepicker
    $_fn_timepicker.call(this.$el,this.options);
  }

  $.extend(Timepicker.prototype,{});
  $.extend(Timepicker,{});

  //jquery-uitimepicker
  widget.install('timepicker',Timepicker);

  return Timepicker

}));